import { any } from "prop-types";
import React, { Component, createContext, useContext } from "react";
import firebase, { db } from "../config/Firebase";
import { StateProps } from "../types/context";
const values = ["projects", "skills", "slides", "testimonials", "contact"];

class State {
  state: StateProps = {};
  constructor(values: string[] = []) {
    values.forEach((el) => {
      this.state[el] = [];
      this.state[`${el}Loaded`] = false;
    });
  }
}
const DataContext = createContext<StateProps>({
  ...Component,
  ...new State(values).state,
  nameToUrl: (name: string) => name.split(" ").join("_").toLowerCase(),
});

export const useData = () => useContext(DataContext);

class DataContextProvider extends Component {
  state: StateProps = {
    ...new State(values).state,
    pageLoaded: false,
    allLoaded: false,
  };
  componentDidMount() {
    this.setState({ db });
    const getData = (type: string, typeLoaded: string) => {
      this.setState({ [typeLoaded]: false });
      const onSnapshot = (snapShot: firebase.firestore.QuerySnapshot) => {
        const array = snapShot.docs.map((doc) => ({
          ...doc.data(),
          id: doc.id,
        }));
        this.setState({ [type]: array, [typeLoaded]: true });
      };
      db.collection(type).onSnapshot(onSnapshot);
    };
    values.forEach((el) => getData(el, el + "Loaded"));
    window.addEventListener("load", () => this.setState({ pageLoaded: true }));
  }
  UNSAFE_componentWillUpdate(
    _prevProps: any,
    prevState: { [x: string]: any; pageLoaded?: any; allLoaded?: any }
  ) {
    const { pageLoaded, allLoaded } = prevState;
    if (
      !values.map((el) => prevState[`${el}Loaded`]).includes(false) &&
      pageLoaded &&
      !allLoaded
    ) {
      const loader = document.querySelector("div.loader_wrapper");
      if (loader) {
        loader.classList.add("drop");
        setTimeout(() => {
          loader.remove();
        }, 1000);
      }
      setTimeout(() => {
        this.setState({ allLoaded: true });
      }, 100);
    }
  }
  nameToUrl = (name: string) => name.split(" ").join("_").toLowerCase();
  render() {
    return (
      <DataContext.Provider value={{ ...this.state, ...this }}>
        {this.props.children}
      </DataContext.Provider>
    );
  }
}

export default DataContextProvider;
